import { useState } from 'react'
import { Helmet } from 'react-helmet-async'
import { useNavigate, useParams } from 'react-router'

import ArrowBackIcon from '@mui/icons-material/ArrowBack'
import { Box, Button, Card, CardHeader, Container, Divider, Stack, TextField, Typography } from '@mui/material'
import {
  DataGrid,
  type GridCellParams,
  type GridEventListener,
  GridFooterContainer,
  GridPagination,
  type GridRowSelectionModel
} from '@mui/x-data-grid'
import { LocalizationProvider, MobileDatePicker } from '@mui/x-date-pickers'
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs'

import { MEETING_NOTES_COLUMN } from 'src/data/meetingNotes'

import type { Meeting } from 'src/models/meeting'

import BulkActions from 'src/content/manage/Conference/Meetings/BulkActions'

import {
  useCreateConferenceRound,
  useDeleteConferenceRounds,
  useGetConferenceRounds
} from 'src/hooks/api/clubConferenceHooks'

const Meetings = () => {
  const navigate = useNavigate()
  const { id, confId } = useParams()
  const clubId = Number(id)
  const conferenceId = Number(confId)
  const { data } = useGetConferenceRounds(clubId, conferenceId)

  const [selectedRoundIds, setSelectedRoundIds] = useState<GridRowSelectionModel>([])
  const [isAddCalendarOpen, setIsAddCalendarOpen] = useState(false)
  const [attendanceDate, setAttendanceDate] = useState<Date>(new Date())
  const deleteConferenceRoundsMutation = useDeleteConferenceRounds(clubId, conferenceId)
  const addNewRoundMutation = useCreateConferenceRound({
    clubId,
    conferenceId,
    startAt: attendanceDate,
    doesAllMemberAttended: true
  })

  const getFormattedDate = (date: Date) => {
    return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`
  }
  const conferenceRoundRows =
    data?.conferenceRounds?.map(round => ({
      ...round,
      round: `${round.round} 회차`,
      startAt: getFormattedDate(new Date(round.startAt))
    })) || []

  const handleCellClick: GridEventListener<'cellClick'> = params => {
    if (params.field === 'round') {
      navigate(`/manage/${clubId}/attendance/${conferenceId}/meeting/${params.id}/manage`)
      return
    } else if (params.field === 'note') {
      navigate(`/manage/${clubId}/attendance/${conferenceId}/meeting/${params.id}/notes`)
      return
    }
  }
  const handleClickNavigateBack: React.MouseEventHandler<HTMLButtonElement> = () => {
    navigate(-1)
  }
  const handleRowSelectionChange = (selectedConferenceIds: GridRowSelectionModel) => {
    setSelectedRoundIds(selectedConferenceIds)
  }
  const handleDelete = () => {
    deleteConferenceRoundsMutation.mutate({ ids: selectedRoundIds as number[] })
  }
  const handleSetAttendance = (date: Date | null) => {
    if (date === null) return
    setAttendanceDate(date)
  }
  const handleCalendarAccept = () => {
    if (attendanceDate == null) return
    addNewRoundMutation.mutate()
  }
  const handleAddCalendarOpen = () => setIsAddCalendarOpen(!isAddCalendarOpen)

  const MeetingsGridFooter = () => (
    <GridFooterContainer>
      <Stack direction="column" width="100%">
        <Button sx={{ margin: '4px auto' }} onClick={handleAddCalendarOpen}>
          회차 추가
        </Button>
        <Divider />
        <GridPagination />
      </Stack>
    </GridFooterContainer>
  )
  return (
    <Box m={3}>
      <Helmet>
        <title>회차목록</title>
      </Helmet>
      <Container>
        <Card>
          <Box sx={{ display: 'flex', flexDirection: 'row', alignItems: 'center', p: 1 }}>
            <Button variant="text" onClick={handleClickNavigateBack} color="primary">
              <ArrowBackIcon />
            </Button>
            {selectedRoundIds.length > 0 ? (
              <BulkActions selectedRoundIds={selectedRoundIds as number[]} handleDelete={handleDelete} />
            ) : (
              <>
                <CardHeader title="회차목록" />
                <Typography variant={'caption'}>회차명을 클릭하면 출석 및 날짜 수정이 가능해요</Typography>
              </>
            )}
          </Box>
          <Divider />
          <Box
            sx={{
              '& .title--bold': {
                fontWeight: 'bold',
                textDecoration: 'underline',
                cursor: 'pointer'
              }
            }}
          >
            <DataGrid
              autoHeight
              checkboxSelection
              rows={conferenceRoundRows}
              columns={MEETING_NOTES_COLUMN}
              onCellClick={handleCellClick}
              getCellClassName={(params: GridCellParams<Meeting>) => {
                if (params.field === 'round') {
                  return 'title--bold'
                } else return ''
              }}
              slots={{
                footer: MeetingsGridFooter
              }}
              disableRowSelectionOnClick
              rowSelectionModel={selectedRoundIds}
              onRowSelectionModelChange={handleRowSelectionChange}
            />
          </Box>
        </Card>
        <LocalizationProvider dateAdapter={AdapterDayjs}>
          <MobileDatePicker
            label="신규 회차 일정"
            open={isAddCalendarOpen}
            onClose={handleAddCalendarOpen}
            value={attendanceDate}
            onChange={handleSetAttendance}
            onAccept={handleCalendarAccept}
            renderInput={params => <TextField sx={{ display: 'none' }} {...params} />}
          />
        </LocalizationProvider>
      </Container>
    </Box>
  )
}

export default Meetings
